.b-about-company {
  width: 100%;
  text-align: center;
  padding: 15px 0 10px 0;
  border-top: 1px solid #dbdbdd;
  border-bottom: 1px solid #dbdbdd;
  margin: -1px 0 -1px 0; }
  .b-about-company__title {
    font-weight: bold;
    padding: 10px 0;
    margin-bottom: 2px;
    margin-top: 20px; }
  .b-about-company__content {
    max-width: 880px;
    padding: 0 15px;
    margin: auto;
    margin-bottom: 23px;
    font-size: 1.125rem;
    color: #676d72;
    font-weight: 300; }
  .b-about-company__achievements {
    padding: 30px 0;
    margin: auto; }
  .b-about-company__achievement {
    display: inline-block;
    padding: 30px 0;
    width: 180px;
    vertical-align: top; }
  .b-about-company__achievement-number {
    font-weight: bold;
    color: #6a0ba7;
    font-size: 2.1875em;
    line-height: 1.3; }
  .b-about-company__achievement-desc {
    padding: 0 30px;
    font-size: 1em; }
  @media (min-width: 992px) {
    .b-about-company__achievement {
      width: 210px; }
    .b-about-company__achievement-number {
      font-size: 3.125em; } }
